/* button */

.btn {
    display: flex;
    align-items: center;
    justify-content: center; 
    text-decoration: none;
    border-radius: var(--border-radius);
    border: 0;
    cursor: pointer;
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
    padding: 1rem 3.5rem;
    text-transform: uppercase;
    white-space: nowrap;
    text-decoration: none;
    transition: 0.2s;
    margin: 1rem auto; /* Center the button horizontally */
}

.btn--primery{
    background: var(--color-primary);
    color: #fff;
}
.btn--primery:hover{
    background: #1a6d91;
}
.btn--secondary{
    background: var(--color-secondary);
    color: #fff;
}
.btn--secondary:hover{
    background: #205E57;
}
.btn--accent{
    background: var(--color-accent);
    color: #fff;
}
.btn--accent:hover{
    background: #936e6e;
}
.btn--block{
    display: block;
    width: 90%;
    margin: 1rem auto; /* Center the block button horizontally */
}
.btn--half {
    width: 50%;
    margin: 1rem auto; /* Center the half-width button horizontally */
}
.btn--outline{
    background: #fff;
    color: var(--color-secondary);
    border: 2px solid var(--color-secondary); 
}
.btn--outline:hover{
    background: var(--color-secondary);
    color: #fff;
}

.btn--goback{
    position: absolute;
    width: 15px;
    height: 30px;
    padding: 2rem;
    color: var(--color-bg-dark);
    left:20px;
    display: flex;
    align-items: center;  
    justify-content: center;
    background-color: transparent;
    margin-top: 2rem;
}
.btn--goback::before{
    content: "←"; 
    font-size: 30px;
    transition: margin 0.15s;
}
.btn--goback:hover::before{
    background-color: transparent;
    color: var(--color-accent);
    margin-right: 20px;
}

.btn-container {
    display: flex;
    justify-content: space-between; /* Space between buttons */
    align-items: center;
    width: 100%;
}

.btn--left {
    margin-left: 4.2rem;
}

.btn--right {
    margin-right: 4rem;
}

@media screen and (max-width:1024px) {
    .btn {
        font-size: 2rem; 
    }
}